import { AuthCoreEvent, isConnected } from '@particle-network/auth-core';
import React, { useCallback, useEffect } from 'react';
import { useModalOptions, useParticleAuth } from '.';
import { useConnect, useEthereum, useSolana } from './hooks';

enum MessageType {
  OpenAccountAndSecurity = 'open-account-and-security',
  GetUserInfo = 'get-user-info',
}

const IframeWalletPlugin = () => {
  const { provider } = useEthereum();

  const { wallet } = useSolana();

  const { modalOptions } = useModalOptions();

  const { connected } = useConnect();

  const { walletEntryPlugin } = useParticleAuth();

  const customEventHandler = useCallback(async (messageType: string) => {
    if (messageType === MessageType.OpenAccountAndSecurity) {
      window.particle?._internal?.openAccountAndSecurity();
    } else if (messageType === MessageType.GetUserInfo) {
      return window.particle?._internal?.getUserInfo?.();
    }
  }, []);

  useEffect(() => {
    if (modalOptions.wallet !== false && walletEntryPlugin) {
      const walletOptions = {
        ...modalOptions.wallet,
        erc4337: modalOptions.erc4337,
        language: modalOptions.language,
        customStyle: {
          ...modalOptions.customStyle,
          supportChains: modalOptions.chains?.map((item) => {
            return {
              id: item.id,
              name: item.name,
              chainType: (item?.custom?.chainType as 'evm' | 'solana') || 'evm',
            };
          }),
        },
      };

      walletEntryPlugin.init(
        {
          projectId: modalOptions.projectId,
          clientKey: modalOptions.clientKey,
          appId: modalOptions.appId,
        },
        walletOptions
      );
      const onConnected = () => {
        walletEntryPlugin.setWalletCore({
          ethereum: provider,
          solana: wallet,
          customEventHandler,
        });
        walletEntryPlugin.walletEntryCreate();
      };
      const onDisconnect = () => {
        walletEntryPlugin.walletEntryDestroy();
      };
      if (typeof window !== 'undefined') {
        window.particleAuth?.on(AuthCoreEvent.ParticleAuthDisconnect, onDisconnect);
        window.particleAuth?.on(AuthCoreEvent.ParticleAuthConnect, onConnected);
      }
      if (isConnected()) {
        walletEntryPlugin.setWalletCore({
          ethereum: provider,
          solana: wallet,
          customEventHandler,
        });
        walletEntryPlugin.walletEntryCreate();
      }
      return () => {
        if (typeof window !== 'undefined') {
          window.particleAuth?.off(AuthCoreEvent.ParticleAuthDisconnect, onDisconnect);
          window.particleAuth?.off(AuthCoreEvent.ParticleAuthConnect, onConnected);
        }
      };
    }
  }, [modalOptions, provider, wallet, customEventHandler, walletEntryPlugin]);

  useEffect(() => {
    console.log('modalOptions changed', modalOptions);
  }, [modalOptions]);

  useEffect(() => {
    console.log('provider changed', provider);
  }, [provider]);

  useEffect(() => {
    console.log('wallet changed', wallet);
  }, [wallet]);

  useEffect(() => {
    console.log('walletEntryPlugin changed', walletEntryPlugin);
  }, [walletEntryPlugin]);

  useEffect(() => {
    console.log('customEventHandler changed', customEventHandler);
  }, [customEventHandler]);

  useEffect(() => {
    if (connected && modalOptions.wallet !== false && walletEntryPlugin) {
      walletEntryPlugin.setWalletCore({
        ethereum: provider,
        solana: wallet,
        customEventHandler,
      });
    }
  }, [connected, provider, wallet, modalOptions.wallet, customEventHandler, walletEntryPlugin]);

  return (
    <div id='auth-core-modal-wallet-plugin' style={{ display: 'none' }}>
      Wallet Plugin
    </div>
  );
};

export default IframeWalletPlugin;
